/* Slider */
.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-track,
.slick-slider .slick-list {
  -webkit-transform: translate3d(0, 0, 0);
  -moz-transform: translate3d(0, 0, 0);
  -ms-transform: translate3d(0, 0, 0);
  -o-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
  display: none;
}

[dir="rtl"] .slick-slide {
  float: right;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

/* Slider */
.slick-loading .slick-list {
  background: #fff url("../img/ajax-loader.gif") center center no-repeat;
}

/* Arrows */
.slick-prev,
.slick-next {
  position: absolute;
  display: block;
  line-height: 0px;
  font-size: 0px;
  cursor: pointer;
  background: transparent;
  color: transparent;
  top: 35%;
  margin-top: -10px;
  padding: 0;
  border: none;
  border: 60px solid #FFF;
  border-top: 35px solid transparent;
  border-bottom: 35px solid transparent;
  border-left-width: 0;
  outline: none;
  transition: 0.3s;
}

.slick-prev:hover, .slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  outline: none;
  background: transparent;
  color: transparent;
}

.slick-prev:hover:before, .slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  opacity: 0.6;
}

.slick-prev.slick-disabled:hover,
.slick-next.slick-disabled:hover {
  border-color: none;
}

.slick-prev:hover {
  border-right-color: #ffe765;
}

.slick-next {
  border-left-width: 60px;
  border-right-width: 0;
}

.slick-next:hover {
  border-left-color: #ffe765;
}

.slick-prev {
  left: -106px;
}

[dir="rtl"] .slick-prev {
  left: auto;
  right: -106px;
}

.slick-prev:before {
  content: "←";
}

[dir="rtl"] .slick-prev:before {
  content: "→";
}

.slick-next {
  right: -106px;
}

[dir="rtl"] .slick-next {
  left: -106px;
  right: auto;
}

.slick-next:before {
  content: "→";
}

[dir="rtl"] .slick-next:before {
  content: "←";
}

/* Dots */
.slick-dots {
  position: absolute;
  bottom: 22px;
  list-style: none;
  display: block;
  text-align: center;
  padding: 0;
  width: 100%;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.slick-dots li button {
  display: block;
  padding: 3px;
  background: none;
  outline: none;
  line-height: 0px;
  font-size: 0px;
  color: red;
  cursor: pointer;
}

.slick-dots li button:before {
  content: "";
  display: block;
  height: 10px;
  width: 10px;
  box-sizing: border-box;
  border-radius: 50%;
  background: #979795;
  transition: all 0.1s ease;
}

.slick-dots li button:hover:before {
  background: #4E4E4E;
}

.slick-dots li.slick-active button:before {
  background: #4E4E4E;
}

