.slick-prev {
    left: -106px;
    [dir="rtl"] & {
        left: auto;
        right: -106px;
    }
    &:before {
        content: $slick-prev-character;
        [dir="rtl"] & {
            content: $slick-next-character;
        }
    }
}

.slick-next {
    right: -106px;
    [dir="rtl"] & {
        left: -106px;
        right: auto;
    }
    &:before {
        content: $slick-next-character;
        [dir="rtl"] & {
            content: $slick-prev-character;
        }
    }
}

/* Dots */

// .slick-slider {
//     margin-bottom: 30px;
// }

.slick-dots {
    position: absolute;
    bottom: 22px;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        cursor: pointer;
        button {
            display: block;
            padding: 3px;
            background: none;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: red;
            cursor: pointer;
            &:before {
                content: "";
                display: block;
                height: 10px;
                width: 10px;
                box-sizing: border-box;
                border-radius: 50%;
                background: #979795;
                transition: all 0.1s ease;
            }
            &:hover {
                &:before {
                   background: #4E4E4E;
                }
            }
        }
        &.slick-active button:before {
            background: #4E4E4E;
        }
    }

}