.galleries {
	@include clr;
	&__item {
		float: left;
		width: 50%;
		position: relative;
		height: em(300,18);
		@include r(805) {
			float: none;
			width: 100%; }
		&:hover {
			cursor: pointer; } }
	&__bg {
		max-width: 100%;
		max-height: 100%;
		width: 100%;
		height: 100%; }
	&__block {
		height: 100%;
		// padding: 1em 0
		text-align: center;
		transition: 0.5s;
		background-color: rgba(4, 191, 191, 0.5);
		position: absolute;
		top: 0;
		left: 0;
		bottom: 0;
		right: 0;
		width: 100%;
		&:hover, &:focus {
			background-color: rgba(4, 191, 191, 0); } }
	&__info {
		@include vcenter;
		color: #FFF;
		&:hover, &:focus {
			color: #FFF; } }
	&__text {
		display: inline-block;
		font-size: 24px;
		font-weight: 400;
		line-height: 24px; } }
